.list {
  display: flex;
}
.item {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 30px;
}
.item:not(:last-child) {
  padding-right: 30px;
  border-right: 1px dashed var(--color-2-01);
}
.link {
  color: var(--color-2);
}

@media (max-width: 800px) {
  .item {
    padding-left: 10px;
  }

  .item:not(:last-child) {
    padding-right: 10px;
  }
}
@media (max-width: 641px) {
  .list {
    display: flex;
    flex-direction: column;
  }
  .item {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
  }
  .item:not(:last-child) {
    padding-right: 0;
    border-right: none;
    border-bottom: 1px dashed var(--color-2-01);
  }
  .link {
    color: var(--color-2);
  }
}
